import { inProgKey } from "./storageConsts"

export default function setStringOfFireData({cofId,key, sofIndex,data}){
    const allInProgData = JSON.parse(localStorage.getItem(inProgKey) || '{}')
    const currentInProgCOF = allInProgData?.[cofId]|| {}
    currentInProgCOF[key][sofIndex] = data
    localStorage.setItem(inProgKey, JSON.stringify({...allInProgData, [cofId]:currentInProgCOF}))
}

export function setMarksmanshipData({cofId,data}){
    const allInProgData = JSON.parse(localStorage.getItem(inProgKey) || '{}')
    const currentInProgCOF = allInProgData?.[cofId]|| {}
    currentInProgCOF['marksmanship'] = data
    localStorage.setItem(inProgKey, JSON.stringify({...allInProgData, [cofId]:currentInProgCOF}))
}
