import { useEffect, useRef, useState } from "react";
import { useParams, useNavigate  } from "react-router-dom";
import styled from 'styled-components'
import { LinkButton, PrimaryButton, SecondaryButton } from "../components/Buttons";
import { Fieldset, InputWrapper, Label } from "../components/FormElements";
import NumberInput from "../components/NumberInput";
import getCurrentSOF from "../storage/getCurrentSOF";
import setStringOfFireData from "../storage/setCurrentCOF";



const ExitButton = styled(LinkButton)`
  margin-top:2rem;
`
export default function COF({getPageData}) {
  let {cofId,sofIndex:sofIndexString} = useParams()
  const navigate = useNavigate()
  const sofIndex = Number(sofIndexString)
  const pageData = getPageData(cofId)
  const lastCourseOfFireIndex = pageData.COF.length-1
  const isLastStringOfFire = sofIndex === lastCourseOfFireIndex

  const currentStringOfFireContent = pageData.COF[sofIndex]
  const currentStringOfFireData = getCurrentSOF({sofIndex,cofId})

  useEffect(()=>{
    setTime(currentStringOfFireData)
  },[sofIndex])

  const [time, setTime] = useState(currentStringOfFireData)
  const formRef = useRef(null)

  function onSubmit(){
    setStringOfFireData({cofId,sofIndex, key:'time', data:time})
    formRef.current.reset()

    setTime('')
    navigate(isLastStringOfFire? `/marksmanship/${cofId}`:`/cof/${cofId}/${sofIndex+1}`)
  }

  function onCancel(e){
    e.preventDefault(e)
    if(!confirm('Are you sure? Your progress will be lost')){return} //eslint-disable-line
    navigate('/')
  }
    return (
      <main style={{ padding: "1rem 0" }}>
        <h2 style={{ color:'#CEFF51' }}>{`${pageData.title} COF`}</h2>
        <p>String of fire: {sofIndex+1}</p>
        <p>Distance: {currentStringOfFireContent.distance}</p>
        <p>Instructions:</p>
        <p style={{textAlign:'center'}}>{currentStringOfFireContent.description}</p>
        <div style = {{margin: '2rem 0', textAlign:'center'}}>
          <form ref={formRef} onSubmit={e=>{
            e.preventDefault();
            e.stopPropagation();
            onSubmit()
            }}>
            <Fieldset>
              <InputWrapper>
                <Label htmlFor = "Time">Time:</Label>
                <NumberInput
                  onChange={(e)=>{setTime(Number(e.target.value))}} 
                  required
                  value={time}
                  type="number"
                  step=".01"
                  autoComplete="off"
                  name ="Time" 
                  id="Time"
                  />
              </InputWrapper>
            </Fieldset>
            <SecondaryButton onClick = {()=>navigate(-1)} type="button">Back</SecondaryButton>
            <PrimaryButton type="submit">Next</PrimaryButton>
            <ExitButton  onClick = {onCancel} type="click">Exit</ExitButton>

          </form>
        </div>
      </main>
    );
  }